body {
  font-family: 'ZCOOL QingKe HuangYou', Arial, Helvetica, sans-serif;
  background-color: black;
  font-size: 2vw;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 3px black;
  text-shadow: 1px 1px 3px black;
  background-image: url(https://logos-download.com/wp-content/uploads/2016/09/Star_Wars_logo-1.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 50%;
}

.score, .title, .level, .game {
  width: 18vw;
  text-align: center;
  margin: 10px;
  position: fixed;
  border: 2px gold solid;
  padding: 5px;
}

.score, .level {
  bottom: 0vw;
}

.score {
  border: 2px gold solid;
  background: rgba(0, 170, 0, .70);
  left: 5%;
}

.level {
  right: 5%;
  background: rgba(0, 132, 181, .70);
}

/* .leftTitle {
  top: 10%;
}

.rightTitle {
  bottom: 10%;
} */

.title, .game {
  background: rgba(104, 104, 104, 0.7);
}

.title {
  color: rgb(255, 255, 255);
  left: 5%;
}

.game {
  right: 5%;
  text-align: left;
}

.modalStyle {
  background-color: black;
}

.levelDown, .levelUp {
  position: fixed;
  top: 49%;
}

.levelDown {
  left: 10%;
}

.levelUp {
right: 10%;
}

button {
  width: 10vw;
  height: 5vw;
  font-size: 2vw;
  font-weight: bolder;
  background-color: gold;
  font-family: 'ZCOOL QingKe HuangYou', Arial, Helvetica, sans-serif;
}