.card {
  border: 0.1rem solid gold;
  position: relative;
  text-align: left;
}

.card > .img-container {
  height: 150px;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 150px;
}

.card > .content {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}